@import "../../App.module.scss";

.navContainer {
    width: 100%;
    height: 3.5rem;
    max-width: 1440px;

    display: flex;
    justify-content: space-between;
    
    padding: 1rem 3.5rem;   
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.125);
    position: fixed;

    h1 {
        font-size: 1.25rem;
        font-weight: 800;
    }

    .theme {
        position: absolute;
        top: 1.25rem;
        right: 3.5rem;
        font-size: 1rem;
        font-weight: 600;

        &:hover {
            cursor: pointer;
        }

        span {
            padding-left: 0.5rem;
        }
    }
}

.navContainer.light {
    background-color: $light-mode-elements;

    h1, .themeIcon, .themeText {
        color: $light-mode-text;
    }

}

.navContainer.dark {
    background-color: $dark-mode-elements;

    h1, .theme {
        color: $dark-mode-text;
    }
}